import React, { Component } from 'react'
class Skills extends Component {
constructor(props)
{
super(props);
this.state={
'myskills':['Java', 'C/C++', 'HTML5','CSS3','JavaScript','REACT JS']
};
}
render() {
return (
<div className="condiv skills">
<h1 className="subtopic"><b>SKILLS</b></h1>
<ul>
{this.state.myskills.map((value)=>{
return <li>{value}</li>
})}
</ul>
</div>
)
}
}
export default Skills